import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import Table from './components/table';
import Choos2 from './components/choos2';
import TowSite from './components/towSite';
import StartSite from './components/startSite';

import Game from './components/memoGame/Game/Game';
import "./mystyle.css";

const dataImport = require("./files/data");

{/*App Class beginn*/}
class App extends Component{

  state = {
      number: 0,
      altProduct:0,
      productNum:0,
      tbl2:0,
      dropDownName: "ABBOTT - XIENCE SIERRA 1",
      dropDownChoosName: "Choose DES 2",
      dropDownShow: "disabled"
  };

   links={
      background: "#462562 url('fotos/hexagon.png') no-repeat center",
         }
    
    getErsteProdukt = () => {
      let numPdf = 0;
      let ersteProdukt = [];

      while (numPdf < 1) {
          let numCat = Object.keys(dataImport[numPdf]).length;
          let numPdfCat = 1;
          ersteProdukt.push(
              <div key={numCat} className="pl-4 pr-4 pb-0 pt-4 mt-0">
                  <button className="text-center btn-block text-white pt-2 pb-2 mb-1 border-0"
                          style={{background: '#462562'}}
                          disabled>{this.state.dropDownName}
                  </button>
              </div>
          )

          while (numPdfCat < numCat) {
              ersteProdukt.push(
                      <div key={numPdfCat} className="pl-4 pr-4 back-design">
                          <Table numTable={numPdf} numCat={numPdfCat}/>
                      </div>
              )
              numPdfCat++;
          }
          numPdf++;
      }
      return ersteProdukt;
    }
    /***************************************************************/
    tableEmpty = () => {
        const Ele = document.getElementById('tblRoot');
        ReactDOM.render(
            <div className="alert alert-secondary">
                <strong>Success!</strong> please click on a product
            </div>, Ele)
    }
    /***************************************************************/
    memorySpiel=()=>{
        ReactDOM.render(<Game/>,document.getElementById('root'));
    }
    /***************************************************************/
    tableElement = (b, tblFormat, num = '') => {
        if (tblFormat !== "choos2") {
            this.tableEmpty()
            if (b !== '') {
                num = (b.target["name"]);
                this.state.dropDownName = (b.target["value"]);
            }


            document.getElementById("dropDownName").innerHTML = this.state.dropDownName;
            /* document.getElementById("dropDownChoosId").className += "show"; */

            this.state.altProduct = num;
            let numObject = Object.keys(dataImport[num]).length;
            let minNum = 1;
            let final = [];

            final.pop();
            final.push(
                <div className="pl-4 pr-4 pb-0 pt-4 mt-0">
                    <button className="text-center btn-block text-white pt-2 pb-2 mb-1 border-0"
                            style={{background: '#462562'}}
                            disabled>{this.state.dropDownName}
                    </button>
                </div>
            )
            while (minNum < numObject) {
                final.push(
                    <div className="pl-4 pr-4 pb-0 back-design">
                        <Table numTable={num} numCat={minNum}/>
                    </div>
                )
                minNum++
            }
            const Ele = document.getElementById('tblRoot');
            ReactDOM.render(final, Ele);
        } else {
            this.tableEmpty()
            num = (b.target["name"]);
            this.state.tbl2=num;
            this.state.dropDownChoosName = (b.target["value"]);
            document.getElementById("dropDownChoosName").innerHTML = this.state.dropDownChoosName;
            let numObject = Object.keys(dataImport[num]).length;
            let minNum = 1;
            let final2 = [];

            final2.pop();
            final2.push(
            <React.Fragment>
                <div className="pt-4 pb-0 pr-4 pl-4 back-design">
                    <div className="row pl-0 pr-0 mb-1">
                        <div className="col-md-2">
                        </div>
                        <div className="col-md-5" style={{paddingLeft:'1.5rem'}}>
                            <button className="text-center btn-block text-white pt-2 pb-2 border-0"
                                    style={{background: '#462562'}}
                                    disabled>{this.state.dropDownName}</button>
                        </div>
                        <div className="col-md-5 pl-0">
                            <button className="text-center btn-block text-white pt-2 pb-2 border-0"
                                    style={{background: '#462562'}}
                                    disabled>{this.state.dropDownChoosName}</button>
                        </div>
                    </div>
                </div>
            </React.Fragment>
            )
            while (minNum < numObject) {
                final2.push(
                    <div>
                        <Choos2 altNumTbl={this.state.altProduct} numTable={num} numCat={minNum}/>
                    </div>  
                )
                minNum++
            }

            if((parseInt(this.state.number)) < (parseInt(this.state.tbl2))){
                final2.push(
                    <div>
                        <a href={"pdf/"+(++this.state.number)+"/product"+(this.state.number)+"-"+(++this.state.tbl2)+".pdf"} className="ml-4 pdf">PDF EXPORT</a>
                    </div>  
                )  
            }
            else{
                final2.push(
                    <div>
                        <a href={"pdf/"+(++this.state.tbl2)+"/product"+(this.state.tbl2)+"-"+(++this.state.number)+".pdf"} className="ml-4 pdf">PDF EXPORT</a>
                    </div>  
                )
            }
      
      const Ele = document.getElementById('tblRoot');
      ReactDOM.render(final2, Ele);
  }
}
/*****************************PRODUCTS TITLE HIER*********************************3*/
renderSubHeaders = (tblFormat) => {
  let subHeaders = [];
  let productFile = Object.values(dataImport);
  let productLength = Object.keys(productFile).length;
  let num=0;
  
  while (num < productLength) {
      let subs = Object.values(dataImport[num][0])
      productFile.map((x, i) => {
          if (subs[i] !== undefined) {
              Object.values(subs[i]).map(y => {
                  if(tblFormat==="choos2"){/**TITLE FOR CHOOS 2"**/
                    subHeaders.push(
                        <button id={tblFormat+num} className="dropdown-item btn-block font-weight-bold pl-2" data-tag={num} name={num}
                                value={y} key={num}
                                style={{width: '442px'}}
                                onLoadCapture={(ev) => this.tableElement(ev, tblFormat)}
                                onMouseUp={(ev) => this.tableEmpty()}
                                onClick={(ev) => this.tableElement(ev, tblFormat)}>{y}
                        </button>)
                  }
                  else{          
                    subHeaders.push(
                        <button id={num} className="dropdown-item btn-block font-weight-bold pl-2" data-tag={num} name={num}
                                value={y} key={num}
                                style={{width: '442px'}}
                                onLoadCapture={(ev) => this.tableElement(ev, tblFormat)}
                                onMouseUp={(ev) => this.tableEmpty()}
                                onClick={(ev) => this.tableElement(ev, tblFormat)}>{y}
                        </button>)
                  }
                  return subHeaders;
              })
          }
          return subHeaders;
      })
      num++;
  }
  return subHeaders;
}
/****************************TOW SITE HIER**********************************2*/
towSiteElement = () => {
    ReactDOM.render(<TowSite nameOfButton={this.state.dropDownName}
                            nameOfProduct={this.renderSubHeaders}
                            nameOfChoosButton={this.state.dropDownChoosName}
                            oneProduct={this.getErsteProdukt}
                    />, document.getElementById("root"));
}
/****************************CLASS RENDER***********************************1*/
    render() {
    return (
        <StartSite towSite={this.towSiteElement} memory={this.memorySpiel}  startSite={this.startSite}/>
    );
    }
}

export default App;
