import React from "react";

const data = require("../files/data");

class Choos2 extends React.Component {

    constructor(altNumTbl, numTable, numCat) {
        super(altNumTbl, numTable, numCat);
        this.dataImport = data[this.props.numTable][this.props.numCat];
        this.dataImportAlt = data[this.props.altNumTbl][this.props.numCat];
        this.keyCollars = "collapsee" + this.props.numTable + this.props.numCat;
    }


    renderHeaderCells() {
        let headerCells = [];
        headerCells.pop()
        Object.keys(this.dataImport).map((x, i) => {
            let items = Object.values(this.dataImport)[i];
            headerCells.push(<span colSpan={Object.keys(items).length} key={i}>{x}</span>);
            return headerCells;
        });
        return headerCells;
    }


    renderSubHeaders() {
        let subHeaders = [];
        let subs = Object.values(this.dataImport);
        let res = Object.values(this.dataImportAlt);
        subHeaders.pop()
        subs.map((x, i) => {
            if (subs[i] !== undefined) {
                let num = 0;
                let kum = 0;
                Object.keys(subs[i]).map(y => {
                        let k = Object.values(subs[i])[num++];
                        let alt = Object.values(res[i])[kum++];
                        if (k.replace(/^.*\./, '') === "jpg" || k.replace(/^.*\./, '') === "png") {
                            subHeaders.push(
                                <React.Fragment>
                                    <td className="col-md-2 border-right border-top p-2 text-left" style={{
                                        lineHeight: 15,
                                        fontSize: '12px',
                                        color: '#004f71',
                                        fontWeight: 600,
                                        fontFamily: 'georgia'
                                    }}>
                                        {y}
                                    </td>
                                    <td className="col-md-5 border-right border-top p-2 text-center"
                                        style={{fontSize: '12px'}}>
                                        <img width="200" height="200" src={"fotos/productIMG/" + alt}/>
                                    </td>
                                    <td className="col-md-5 border-right border-top p-2 text-center"
                                        style={{fontSize: '12px'}}>
                                        <img width="200" height="200" src={"fotos/productIMG/" + k}/>
                                    </td>

                                </React.Fragment>)
                        } else {
                            subHeaders.push(
                                <React.Fragment>
                                    <td className="col-md-2 border-right border-top p-2 text-left"
                                        style={{fontSize: '12px', color: '#004f71', fontWeight: 600, fontFamily: 'georgia'}}>
                                        {y}
                                    </td>
                                    <td className="col-md-5 border-right border-top p-2 text-center"
                                        style={{fontSize: '12px', fontWeight: 600, fontFamily: 'georgia'}}>
                                        {alt}
                                    </td>
                                    <td className="col-md-5 border-right border-top p-2 text-center"
                                        style={{fontSize: '12px', fontWeight: 600, fontFamily: 'georgia'}}>
                                        {k}
                                    </td>
                                </React.Fragment>
                            )
                        }
                        return subHeaders;
                    }
                )
            }
            return subHeaders;
        })
        return subHeaders;
    }


    renderResults() {
        let results = [];
        results.pop()
        let res = Object.values(this.dataImport);
        res.map((x, i) => {
            if (res[i] !== undefined) {
                Object.values(res[i]).map(y => {
                    if (y.replace(/^.*\./, '') === "jpg") {
                        results.push(<div className="border-top pb-2 pt-2"><img width="200" height="200"
                                                                                src={"fotos/productIMG/" + y}/></div>);
                        return results;
                    } else {
                        results.push(<div className="border-top pb-2 pt-2">{y}</div>)
                        return results;
                    }
                });
            }
            return results;
        });
        return results;
    }

    render() {
        return (
            <React.Fragment>
                <div className="pr-4 pl-4 back-design">
                    <div id="accordion" className="accordion">
                        <div className="card">
                            <div className="card-header" style={{background: '#004f71'}}>
                                <a className="card-link text-white position-relative d-block text-decoration-none" data-toggle="collapse"
                                   href={"#" + this.keyCollars}>
                                    {this.renderHeaderCells()}
                                </a>
                            </div>
                            <div id={this.keyCollars} className="collapse show" data-parent="#accordion">
                                <div className="card-body">
                                    <table
                                        className="table border-bottom border-left table-responsive-md text-center">
                                        <tbody>
                                        <tr className="row pl-3 pr-3 pt-0 ">
                                            {this.renderSubHeaders()}
                                        </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Choos2;



