import React from "react";

const data = require("../files/data");

class Table extends React.Component {
    constructor(numTable, numCat) {
        super(numTable, numCat);
        this.dataImport = data[this.props.numTable][this.props.numCat];
        this.keyCollars = "collapse" + this.props.numTable + this.props.numCat;


        this.state={
            display:'block',
            background:"#004f71 url('fotos/down.png') no-repeat center",
            width:'48px',
            height:'48px',
            right:'0px',
            top:'0px',
            backgroundSize: '25px 15px'
        }
    }
/*******************************************************************************/
    renderHeaderCells() {
        let headerCells = [];
        headerCells.pop()
        Object.keys(this.dataImport).map((x, i) => {
            let items = Object.values(this.dataImport)[i];
            headerCells.push(<span colSpan={Object.keys(items).length} key={i}>{x}</span>);
            return headerCells;
        });
        return headerCells;
    }
/*******************************************************************************/
    renderSubHeaders() {
        let subHeaders = [];
        let subs = Object.values(this.dataImport);

        subHeaders.pop()
        subs.map((x, i) => {
                if (subs[i] !== undefined) {
                    let num = 0;
                    Object.keys(subs[i]).map(y => {
                            let k = Object.values(subs[i])[num++]
                            if (k.replace(/^.*\./, '') === "jpg" || k.replace(/^.*\./, '') === "png") {
                                subHeaders.push(
                                    <React.Fragment>
                                        <td className="col-md-4 border-right border-top p-2 text-sm-left" style={{lineHeight:15,fontSize:'12px',color:'#004f71',fontWeight:600,fontFamily:'georgia'}}>
                                            {y}
                                        </td>
                                        <td className="col-md-8 border-right border-top p-2 text-center" style={{fontSize:'12px'}}>
                                            <img width="200" height="200" src={"fotos/productIMG/" + k}/>
                                        </td>
                                    </React.Fragment>
                                )
                            }
                            else{
                                subHeaders.push(
                                    <React.Fragment>
                                        <td className="col-md-4 border-right border-top p-2 text-sm-left" style={{fontSize:'12px',color:'#004f71',fontWeight:600,fontFamily:'georgia'}}>
                                            {y}
                                        </td>
                                        <td className="col-md-8 border-right border-top p-2 text-center" style={{fontSize:'12px',fontWeight:600,fontFamily:'georgia'}}>
                                            {k}
                                        </td>
                                    </React.Fragment>
                                )
                            }
                            return subHeaders;
                        }
                    )
                }
                return subHeaders;
            }
        )
        return subHeaders;
    }
/***************************************CALCULATE RESULT FOR PRODUCTS VALUES****************************************/
    renderResults() {
        let results = [];
        results.pop()
        let res = Object.values(this.dataImport);
        res.map((x, i) => {
            if (res[i] !== undefined) {
                Object.values(res[i]).map(y => {
                    if (y.replace(/^.*\./, '') === "jpg") {
                        results.push(<div className="border-top pt-2 pt-2 pl-2 text-center"><img width="200"
                                                                                                 height="200"
                                                                                                 src={"fotos/productIMG/" + y}/>
                        </div>);
                        return results;
                    } else {
                        results.push(<div className="border-top pb-2 pt-2 pl-2 text-center">{y}</div>)
                        return results;
                    }
                });
            }
            return results;
        });
        return results;
    }
/************************************CLASS RENDER HIER*******************************************/
    render() {
        return (
            <React.Fragment>
                <div id="accordion" className="accordion">
                    <div className="card">
                        <div className="card-link card-header position-relative collapsed" style={{background:'#004f71'}}>
                            <a className="text-white d-block text-decoration-none position-relative" data-toggle="collapse"
                               href={"#" + this.keyCollars}>
                                {this.renderHeaderCells()}
                            </a>
                            <i className="position-absolute"></i>
                        </div>
                        <div id={this.keyCollars} className="collapse show" data-parent="#accordion">
                            <div className="card-body">
                                <table
                                    className="table border-bottom border-left table-responsive-md text-center">
                                    <tbody>
                                    <tr className="row pl-3 pr-3 pt-0 ">
                                        {this.renderSubHeaders()}
                                    </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
}

export default Table;



